import React, {useState, useEffect} from 'react'
import { PanelHeader, Panel, Card, Input, Checkbox, Button, Group, Header, Spinner} from '@vkontakte/vkui'
import {Icon20ArticleOutline} from '@vkontakte/icons'

import UserUrl from './UserUrl'
import bridge from '@vkontakte/vk-bridge'

import './Home.css'

const Home = ({id, fetchedUser}) => {
    const [userLinks, setUserLinks] = useState(<Spinner size="medium" style={{ margin: '20px 0' }} />)

    useEffect(() => {
        const start = async () => {
            const token = await bridge.send('VKWebAppGetAuthToken', {
                app_id: 7790406,
                scope: '',
              })
        
            const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
            method: 'utils.getLastShortenedLinks',
            request_id: '324nnefj',
    
            params: {
              v: '5.95',
              count: 50,
              offset: 0,
              access_token: token.access_token,
            },
            })
            setUserLinks(<UserUrl links={photos.response}/>)
        }
          start()
    }, [])

    return (
    <Panel id={id}>
        <PanelHeader>Сокращение cсылок</PanelHeader>
        <Card mode="shadow" style={{margin: 5, padding: 5, fontSize: 14, textAlign: 'center'}}>
            <div >
На этой странице вы можете сделать из длинной и сложной ссылки простую.
Такие ссылки удобнее использовать в ваших записях и сообщениях.
            </div>
        </Card >
        <Input style={{margin: 5}} type="text" placeholder="Ссылка, которую вы хотите сократить" />
        <Checkbox>Статистика ссылки видна только вам</Checkbox>
        <Button size="m" style={{margin: 5}}>Сократить</Button>
        <Group>
        <Header mode="secondary">Недавно сокращённые ссылки</Header>
        {userLinks}
        </Group>
    </Panel>
    )
}

export default Home