import React, {useEffect, useState} from 'react'

import qr from '@vkontakte/vk-qr';
import bridge from '@vkontakte/vk-bridge'
import { Icon20CancelCircleFillRed, Icon20CopyOutline } from '@vkontakte/icons';
import './UserUrl.css'
import { Button } from '@vkontakte/vkui';

const UserUrl = ({links}) => {
    const [linksGrid, setLinksGrid] = useState(null)
    useEffect(() => {
        console.log(links);
       
        let items = []
        links.items.map(x => {
            const qr_code = qr.createQR(x.short_url, {
                qrSize: 256,
                isShowLogo: true,
                isShowBackground: true,
                backgroundColor: '#ffffff'
            });
            let data = new Date(x.timestamp * 1000)
            let date = `${data.getDate() < 10 ?
                '0' + data.getDate() :
                 data.getDate()}.${data.getMonth() < 10 ?
                    "0" + (data.getMonth() + 1) : 
                    data.getMonth() + 1 }.${data.getFullYear()} ${data.getHours() < 10 ?
                        "0" + data.getHours() :
                         data.getHours()}:${data.getMinutes() < 10 ?
                            "0" + data.getMinutes() :
                             data.getMinutes()}`
            items.push(
                <div key={Math.round(Math.random() * 1000000)} className='links-grid-items'>
                    <div className='links-qr-code' dangerouslySetInnerHTML={{
                        __html: qr_code
                    }}></div>
                    <div className='links-body'>
                        <div className='links-short-url'>{x.short_url}</div>
                        <div className='links-url'>{x.url}</div>
                        <div className='links-date'>{date}</div>
                    </div>
                    <Button mode="tertiary"><p className="links-check-qr">Показать</p></Button>
                    <div className='links-buttons'>
                        <Button size="s" style={{margin: 5}}>Копировать</Button>
                        <Button  size="s" mode="destructive" style={{margin: 5}}>Удалить</Button>
                    </div>

                </div>
            )
        })
        setLinksGrid(items)
    }, [links])

    return linksGrid
}

export default UserUrl